html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

header {
  display: flex;
  height: 100vh;
}

header .left-header {
  display: flex;
  flex-direction: column;
  width: 100%;
}

header .left-header__top {
  position: relative;
  height: 25%;
  display: flex;
  align-items: flex-end;
}

header .left-header__top img {
  position: absolute;
  bottom: 0;
  right: -225px;
}

@media (max-width: 1399px) {
  header .left-header__top img {
    right: 0;
  }
}

header .left-header__middle {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-left: 130px;
  height: 68.1%;
}

header .left-header__middle h1 {
  margin-bottom: 60px;
  margin-top: 106px;
}

header .left-header__bottom {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #1F1F1F;
  height: 6.9%;
  width: 100%;
}

header .left-header__bottom a {
  position: relative;
  width: 25%;
  text-align: center;
}

header .left-header__bottom a:after {
  content: "";
  height: 30.48px;
  border-right: 1px solid #9D9D9D;
  position: absolute;
  right: 0;
  top: -6px;
}

header .left-header__bottom a:last-child:after {
  border: 0;
}

header .right-header {
  width: auto;
  height: 100vh;
}

@media (max-width: 1399px) {
  header .right-header {
    display: none;
  }
}

.about-me {
  height: 100vh;
  box-sizing: border-box;
  padding: 70px 0 78px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.about-me__double {
  display: flex;
  justify-content: center;
}

.about-me__double > div {
  width: 50%;
}

@media (max-width: 1199px) {
  .about-me__double {
    flex-direction: column;
    align-items: center;
  }
}

.about-me__photo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-me__description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.about-me__description h2 {
  margin-bottom: 65px;
}

@media (max-width: 1199px) {
  .about-me__description h2 {
    padding-left: 0;
  }
}

.about-me__description div {
  padding-left: 130px;
  width: 463px;
}

.about-me__description div p:first-child {
  margin-bottom: 40px;
}

@media (max-width: 1199px) {
  .about-me__description div {
    padding-left: 0;
  }
}

.about-me__styles h2 {
  margin-bottom: 65px;
}

.about-me__slider {
  padding-left: 130px;
}

.about-me__slider .slick-slide:focus {
  outline: none;
}

.contacts {
  height: 100vh;
  box-sizing: border-box;
  padding: 78px 0;
  background: url("../img/boy.png");
  background-color: #000000;
  background-position-x: 100%;
  background-position-y: 100%;
  background-repeat: no-repeat;
}

@media (max-width: 1199px) {
  .contacts {
    height: auto;
  }
}

.contacts__right {
  width: 50%;
}

.contacts__right h1 {
  margin-bottom: 168px;
}

@media (max-width: 1199px) {
  .contacts__right h1 {
    margin-bottom: 50px;
  }
}

.contacts__right .d-row, .contacts__right .s-row {
  width: 100%;
}

.contacts__right .d-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 66px;
}

.contacts__right .d-row input {
  width: 47%;
}

.contacts__right .s-row {
  margin-bottom: 100px;
}

.contacts__right .s-row input {
  width: 100%;
}

@media (max-width: 1199px) {
  .contacts__right .s-row {
    margin-bottom: 50px;
  }
}

.contacts__right input[type="text"] {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  outline: none;
  border-bottom: 1px solid #FFFFFF;
  box-sizing: border-box;
  padding: 5px 15px 12px 20px;
}

.contacts__right input[type="text"]:placeholder-shown {
  color: #FFFFFF;
}

.contacts__right input[type="text"]::placeholder {
  font-family: 'Proxima Nova Regular';
  color: #FFFFFF;
  font-size: 14px;
  line-height: 22px;
}

@media (max-width: 1199px) {
  .contacts__right {
    width: 100%;
    margin-bottom: 50px;
  }
}

.contacts__main {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 86px 130px;
  align-items: center;
}

@media (max-width: 1199px) {
  .contacts__main {
    flex-direction: column;
    align-items: center;
    padding: 25px 100px;
  }
}

.contacts__left {
  padding-left: 50px;
  align-self: flex-end;
  margin-bottom: 20px;
}

.contacts__left .connect {
  margin-bottom: 110px;
}

.contacts__left .connect > div {
  margin-bottom: 50px;
  position: relative;
}

.contacts__left .connect > div i {
  position: absolute;
  font-size: 18px;
  left: -45px;
  top: 5px;
  color: #fff;
}

.contacts__left .connect__title {
  margin-bottom: 15px;
}

.contacts__left .connect__text {
  text-decoration: none;
}

@media (max-width: 1199px) {
  .contacts__left .connect {
    margin-bottom: 80px;
  }
}

.contacts__left .socials {
  display: flex;
  justify-content: space-between;
}

.contacts__left .socials a {
  color: #FFFFFF;
  font-size: 18px;
}

@media (max-width: 1199px) {
  .contacts__left {
    order: 2;
    align-self: center;
  }
}

footer {
  height: 100px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

footer p {
  font-family: 'Proxima Nova Regular';
  font-size: 14px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #4C4C4C;
  margin-right: 100px;
}

